import styled from "styled-components";
import React, { useEffect } from "react";

const ListContainer = styled.div`
  margin:200px auto;
  width:600px;
  height:400px;
`

const List = styled.div`
  display:flex;
  flex-direction:column;
`

const Item = styled.div`
  border-bottom:2px solid grey;
  border-left:2px solid grey;
  border-right:2px solid grey;
  margin:0px auto;
  width:500px;
  display:flex;
  justify-content:space-between;
  text-align:left;
`
const ItemDemo = styled.div`
  border:2px solid grey;
  margin:0px auto;
  width:500px;
  display:flex;
  justify-content:space-between;
  text-align:left;
`

const Region = styled.div`
  width:50px;
  padding:2px;
`

const Town = styled.div`
width:50px;
border-left:2px solid grey;
padding:2px;
`

const Name = styled.div`
width:200px;
border-left:2px solid grey;
padding:2px;
`

function App() {
  const [item,setItem] = React.useState([])
  useEffect(()=>{
    fetch(
      "https://my-json-server.typicode.com/RickyLee23/interview-test/db"
    ).then((res) => res.json().then((json) => setItem(json.XML_Head.Infos.Info)));
  },[])
  
  return (
    <div className="App">
      <ListContainer>
        <List>
          <ItemDemo>
            <Region>Region</Region>
            <Town>Town</Town>
            <Name>Name</Name>
          </ItemDemo>
          {
            item.map(items => (
              <Item>
                <Region>{items.Region}</Region>
                <Town>{items.Town}</Town>
                <Name>{items.Name}</Name>
              </Item>
              )
            )
          }
        </List>
      </ListContainer>
    </div>
  );
}

export default App;
